
import React from 'react'
import './button.css'
import { Link } from 'react-router-dom'
const STYLES = ['btn--primary', 'btn--outline', 'btn--test'];

const SIZES = ['btn--medium', 'btn--large'];

export const Button = ({
    children,
    type,
    onClick,
    buttonStyle,
    buttonSize
    
}) => {

    const checkButtonStyle = STYLES.includes(buttonStyle) ? buttonStyle : STYLES[0];
    const checkButtoneSize = SIZES.includes(buttonSize) ? buttonSize : SIZES[0];

return(
    <Link to='/Book' className='btn-mobile'>
        <button className={`btn ${checkButtonStyle} ${checkButtoneSize}`}
            onClick={onClick}
            type={type}>

            
        {children}

        </button>

    </Link>

)
}