import React, { useState, useEffect } from 'react';
import './ExchangeRatesComponent.css';

const ExchangeRatesComponent = () => {
  const [exchangeRates, setExchangeRates] = useState({});
  const [baseCurrency, setBaseCurrency] = useState('USD');
  const [targetCurrency, setTargetCurrency] = useState('EUR');
  const [amount, setAmount] = useState(''); // Initialize with an empty string
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState(null);
  const [visibleRate, setVisibleRate] = useState(false); // State to manage visibility of exchange rate

  useEffect(() => {
    if (amount && !isNaN(parseFloat(amount))) {
      fetchExchangeRates();
    }
  }, [baseCurrency, targetCurrency, amount]);

  const fetchExchangeRates = async () => {
    setLoading(true);
    try {
      const response = await fetch(`https://api.exchangerate-api.com/v4/latest/${baseCurrency}`);
      if (response.ok) {
        const data = await response.json();
        setExchangeRates(data.rates);
        setError(null);
        setVisibleRate(true); // Set visibility to true when exchange rates are fetched
      } else {
        setError('Failed to fetch exchange rates');
      }
    } catch (error) {
      setError('Error fetching exchange rates');
    } finally {
      setLoading(false);
    }
  };

  const handleBaseCurrencyChange = (e) => {
    setBaseCurrency(e.target.value);
  };

  const handleTargetCurrencyChange = (e) => {
    setTargetCurrency(e.target.value);
  };

  const handleAmountChange = (e) => {
    setAmount(e.target.value);
  };

  const handleSwitchCurrencies = () => {
    // Swap the values of baseCurrency and targetCurrency
    const temp = baseCurrency;
    setBaseCurrency(targetCurrency);
    setTargetCurrency(temp);
  };

  return (
    <div className='exchange-rate-container'>
      <h2>Exchange Rates</h2>
      <div className="currency-selectors">
        <label className='label'>
          Base Currency:
          <select value={baseCurrency} onChange={handleBaseCurrencyChange}>
            <option value="USD">US Dollar</option>
            <option value="EUR">Euro</option>
            <option value="JPY">Japanese Yen</option>
            <option value="CNY">Chinese Yuan</option>
            <option value="KRW">Korean Won</option>
            <option value="AUD">Australian Dollar</option>
            <option value="CAD">Canadian Dollar</option>
            <option value="THB">Thai Baht</option>
          </select>
        </label>
        <label className='label'>
          Target Currency:
          <select value={targetCurrency} onChange={handleTargetCurrencyChange}>
            <option value="USD">US Dollar</option>
            <option value="EUR">Euro</option>
            <option value="JPY">Japanese Yen</option>
            <option value="CNY">Chinese Yuan</option>
            <option value="KWN">Korean Won</option>
            <option value="AUD">Australian Dollar</option>
            <option value="CAD">Canadian Dollar</option>
            <option value="THB">Thai Baht</option>
          </select>
        </label>
        <label className='label'>
          Amount:
          <input type="number" value={amount} onChange={handleAmountChange} />
        </label>
      </div>
      <button className="switch-button" onClick={handleSwitchCurrencies}>
        <span className="switch-icon">&#x21bb;</span> {/* Unicode character for a clockwise open circle arrow */}
      </button>
      <div className="exchange-rate-info">
        <h2>Exchange Rate</h2>
        <div className="rate-container">
          <ul>
            <li>
              {amount} {baseCurrency} = {visibleRate ? (amount * exchangeRates[targetCurrency]).toFixed(2) : '...'} {targetCurrency}
            </li>
          </ul>
        </div>
      </div>
      {loading && <div>Loading...</div>}
      {error && <div>Error: {error}</div>}
    </div>
  );
};

export default ExchangeRatesComponent;
